const _singleZeroByte = new Uint8Array([0x00]);
const isMacDesktopChrome = BrowserDetector.browserName === 'Chrome' && (/Macintosh/i).test(navigator?.userAgent);
const isAndroidChrome = BrowserDetector.browserName === 'Chrome' && (/Android/i).test(navigator?.userAgent);
let _pFrameNumber = 1;

function surrogateVideoIFramePerCodec(codec: string) : ArrayBuffer | undefined {
  switch (codec) {
    case 'H264': {
      if (isMacDesktopChrome) {
        return _dummy2x2VideoIFramePerCodec[codec]['buffer'];
      }

      if (isAndroidChrome) {
        _pFrameNumber = 1;
      }

      return _dummy82x82H264['I']['buffer'];
    }

    case 'VP8': {
      return _dummy2x2VideoIFramePerCodec[codec]['buffer'];
    }

    case 'VP9': {
      return _dummy2x2VideoIFramePerCodec[codec]['buffer'];
    }

    default: {
      return undefined;
    }
  }
}

function surrogateVideoPFramePerCodec(codec: string) : ArrayBuffer | undefined {
  switch (codec) {
    case 'H264': {
      if (isMacDesktopChrome) { // NOTE: may not need this if( ! isAndroidChrome) defaults to this
        return _dummy2x2VideoPFramePerCodec[codec]['buffer'];
      }

      // X if (isAndroidChrome) {
      const buffer = setDummy82x82H264PFrameFrameNumber(_pFrameNumber);

      _pFrameNumber++;

      return buffer;
      // X }
      // X
      // X return _dummy82x82H264['P']['buffer'];
    }

    case 'VP8': {
      return _dummy2x2VideoPFramePerCodec[codec]['buffer'];
    }

    case 'VP9': {
      return _dummy2x2VideoPFramePerCodec[codec]['buffer'];
    }

    default: {
      return undefined;
    }
  }
}

function surrogateAudioSilentPerCodec(codec: string) : ArrayBuffer | undefined {
  switch (codec) {
    case 'opus': {
      return _dummyAudioSilentPerCodec[codec]['buffer'];
    }

    default: {
      return undefined;
    }
  }
}

function invalidFrame(codec: string, type: string) : ArrayBuffer | undefined {
  // X if (codec === 'H264' && isAndroidChrome) {
  if (codec === 'H264') {
    if (type === 'key') {
      console.log('NIKNIK: KEY _pFrameNumber:', _pFrameNumber);
      // X_pFrameNumber = 1;
      // Xreturn undefined;
    }

    if (_pFrameNumber > 2) {
      console.log('NIKNIK: DELTA _pFrameNumber:', _pFrameNumber);
      _pFrameNumber = 1;

      return undefined;
    }

    const buffer = setDummy82x82H264PFrameFrameNumber(_pFrameNumber);

    _pFrameNumber++;

    return buffer;
  }

  return _singleZeroByte['buffer'];
}

let firstTimeHere = 0;

function setDummy82x82H264PFrameFrameNumber(frameNumber) {
  const byte1 = 0x9a | ((frameNumber & 0x0e) >> 1);
  const byte2 = 0x20 | ((frameNumber & 0x01) << 7);
  const buffer = _dummy82x82H264['P']['buffer'];

  if (!firstTimeHere) {
    firstTimeHere = 1;
    console.log('NIKBYTES: b1:', buffer[_dummy82x82H264PFrameFrameNumberIndex], ' b2:', buffer[_dummy82x82H264PFrameFrameNumberIndex + 1]);
  }

  buffer[_dummy82x82H264PFrameFrameNumberIndex] = byte1;
  buffer[_dummy82x82H264PFrameFrameNumberIndex + 1] = byte2;

  return buffer;
}

